import axios from "axios";

//获取该时间段内的执行情况数据
const getTodoListApi = (pageNum, pageSize) => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/todolist',
        headers:{
            "Content-Type": "application/json",
        },
        params: {
            pageNum: pageNum,
            pageSize: pageSize
        },
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

//获取人员列表
const getUserListApi = () => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/findAll',
        headers:{
            "Content-Type": "application/json",
        }
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

//提交选择的被评估人
const getEvaluatedListApi = (userIds) => new Promise((resolve, reject) =>{
    let data = {
        userIds: userIds
    };
    axios({
        method:'post',
        url:'/api/add/evaluated',
        headers:{
            "Content-Type": "application/json",
        },
      data: JSON.stringify(data),
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

// 获取当前需要打分的人（即我是谁的自主评估人）
const getAssessorApi = () => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/find/assessor',
        headers:{
            "Content-Type": "application/json",
        }
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})
export{
    getTodoListApi,
    getUserListApi,
    getEvaluatedListApi,
    getAssessorApi
}