<!-- 管理员/超级管理员--用户管理 -->
<script setup>
import { reactive, ref, onMounted, onUnmounted } from "vue";
import { message } from 'ant-design-vue'
import { getUserInfoListApi, getDepartmentListApi, getDeleteUserApi, getUserInfoExcelApi, getDeleteUserManyApi, getAddUserApi, getUsersExcel } from "@/api/userManage";
import { getUserListApi } from '@/api/todoList'
import dayjs from "dayjs";
import { getEditPersonalInfoApi } from "@/api/personalInfo"
import * as XLSX from 'xlsx';
import { notification } from 'ant-design-vue'

// 获取用户角色
const userRole = ref(localStorage.getItem('userRole'));
const isSuperAdmin = ref(false)
if (userRole.value === '3') {
    isSuperAdmin.value = true
}

// 搜索框表单数据
const serchInfo = reactive({
    name: null,
    department: null,
    role: null,
});
// 定义搜索功能提交函数
const searchSubmit = () => {
    pagination.current = 1 // 每次搜索将当前页数变为1
    // 调用接口进行搜索
    getUserInfoList()
};

// 分页配置
const pagination = reactive({  // 表格页码相关设置
    pageSize: 14, //每页最多条目数
    current: 1, //当前页码
    showSizeChanger: false,
    total: 0, //总条目数
    showTotal: (total, range) => `总共 ${total} 条数据`, // 在这里定义显示总数据量的格式
})

// 表格的列配置，更新为所需的字段
const columns = ref([
    {
        title: '序号',
        dataIndex: 'index',
        key: 'index',
        fixed: 'left',  // 固定在左侧
        align: 'center',
        width: 65,
    },
    {
        title: '姓名*',
        dataIndex: 'name',
        key: 'name',
        fixed: 'left',  // 固定在左侧
        align: 'center',
        width: 100,
    },
    {
        title: '工号*',
        dataIndex: 'workNum',
        key: 'workNum',
        align: 'center',
        width: 80,

    },
    {
        title: '部门*',
        dataIndex: 'department',
        key: 'department',
        align: 'center',
        width: 90,

    },
    {
        title: '主管1',
        dataIndex: 'supervisorName1',
        key: 'supervisorName1',
        align: 'center',
        width: 100,
    },
    {
        title: '主管2',
        dataIndex: 'supervisorName2',
        key: 'supervisorName2',
        align: 'center',
        width: 100,
    },
    {
        title: '主管3',
        dataIndex: 'supervisorName3',
        key: 'supervisorName3',
        align: 'center',
        width: 100,
    },
    {
        title: 'HRBP',
        dataIndex: 'hrName',
        key: 'hrName',
        align: 'center',
        width: 100,
    },
    {
        title: '入职时间*',
        dataIndex: 'hireDate',
        key: 'hireDate',
        align: 'center',
        width: 110,
        render: (text) => {
            return dayjs(text).format('YYYY-MM-DD');  // 格式化日期为 YYYY-MM-DD
        }
    },
    {
        title: '常驻地',
        dataIndex: 'residence',
        key: 'residence',
        align: 'center',
        width: 80,
    },
    {
        title: '手机号*',
        dataIndex: 'phoneNumber',
        key: 'phoneNumber',
        align: 'center',
        width: 110,
    },
    {
        title: '邮箱*',
        dataIndex: 'email',
        key: 'email',
        align: 'center',
        width: 170,
    },
    {
        title: '业务类型*',
        dataIndex: 'businessType',
        key: 'businessType',
        align: 'center',
        width: 90,
    },
    {
        title: 'LXYZ类型*',
        dataIndex: 'lxyz',
        key: 'lxyz',
        align: 'center',
        width: 100,
    },
    {
        title: '超级管理员',
        dataIndex: 'superAdminName',
        key: 'superAdminName',
        align: 'center',
        width: 120,
    },
    {
        title: '一级管理员',
        dataIndex: 'firstAdminName',
        key: 'firstAdminName',
        align: 'center',
        width: 120,
    },
    {
        title: '二级管理员',
        dataIndex: 'secondAdminName',
        key: 'secondAdminName',
        align: 'center',
        width: 120,
    },
    {
        title: '系统角色*',
        dataIndex: 'role',
        key: 'role',
        align: 'center',
        width: 110,
    },
    {
        title: '操作',
        key: 'action',
        fixed: 'right',  // 固定在右侧
        align: 'center',
        width: 160,
        // slots: { customRender: 'action' }
    },
])

// 表格数据
const tableData = ref([])
const scrollHeight = ref('60vh');  // 定义滚动区域高度

// 动态更新高度
const updateScrollHeight = () => {
    const screenHeight = window.innerHeight;
    const containerHeight = screenHeight - 50 - 10; // 假设父容器需要减去固定的偏移量
    if (screenHeight < 760) {
        scrollHeight.value = `${containerHeight * 0.62}px`;
    } else {
        scrollHeight.value = `${containerHeight * 0.69}px`;
    }
};

// 获取用户表
const getUserInfoList = () => {
    getUserInfoListApi(pagination.current, pagination.pageSize, serchInfo.department, serchInfo.role, serchInfo.name).then(res => {
        if (res.data.code === 200) {
            // console.log('获取用户信息成功',res.data)
            // 设置分页总数
            pagination.total = res.data.data.total;

            // 清空 data.value，避免多次追加导致重复数据
            tableData.value = [];

            // 赋值数据
            for (let i = 0; i < res.data.data.data.length; i++) {
                // 处理角色
                const roleNew = ref('')
                if (res.data.data.data[i].role === 0) {
                    roleNew.value = '普通用户'
                } else if (res.data.data.data[i].role === 1) {
                    roleNew.value = '一级管理员'
                } else if (res.data.data.data[i].role === 2) {
                    roleNew.value = '二级管理员'
                } else {
                    roleNew.value = '超级管理员'
                }
                tableData.value.push({
                    index: pagination.current * pagination.pageSize - pagination.pageSize + i + 1,
                    id: res.data.data.data[i].id,
                    name: res.data.data.data[i].name,
                    username: res.data.data.data[i].username,
                    workNum: res.data.data.data[i].workNum,
                    department: res.data.data.data[i].department,
                    supervisor1: res.data.data.data[i].supervisor1,
                    supervisor2: res.data.data.data[i].supervisor2,
                    supervisor3: res.data.data.data[i].supervisor3,
                    hr: res.data.data.data[i].hr,
                    supervisorName1: res.data.data.data[i].supervisorName1,
                    supervisorName2: res.data.data.data[i].supervisorName2,
                    supervisorName3: res.data.data.data[i].supervisorName3,
                    hrName: res.data.data.data[i].hrName,
                    firstAdmin: res.data.data.data[i].firstAdmin,
                    secondAdmin: res.data.data.data[i].secondAdmin,
                    superAdmin: res.data.data.data[i].superAdmin,
                    firstAdminName: res.data.data.data[i].firstAdminName,
                    secondAdminName: res.data.data.data[i].secondAdminName,
                    superAdminName: res.data.data.data[i].superAdminName,
                    hireDate: res.data.data.data[i].hireDate,
                    residence: res.data.data.data[i].residence,
                    phoneNumber: res.data.data.data[i].phoneNumber,
                    email: res.data.data.data[i].email,
                    businessType: res.data.data.data[i].businessType,
                    lxyz: res.data.data.data[i].lxyz,
                    password: res.data.data.data[i].password,
                    role: roleNew.value
                })
            }
        } else {
            message.error('获取用户列表失败！' + res.data.msg)
        }
    }).catch(err => {
        message.error('请求失败！'+ err);
    })
}

const userOptions = ref([{
    value: '',
    key: ''
}])

// 自定义的模糊匹配搜索函数
const filterOption = (inputValue, option) => {
    if (!inputValue) return true;
    return option.label.toLowerCase().indexOf(inputValue.toLowerCase()) >= 0;
};

// 获取人员列表
const getUserList = () => {
    getUserListApi().then(res => {
        if (res.data.code === 200) {
            userOptions.value = [{
                value: '',
                key: 0
            }]
            for (let i = 0; i <= res.data.data.length - 1; i++) {
                userOptions.value.push({
                    value: res.data.data[i].name + '(' + res.data.data[i].department + ')',
                    key: res.data.data[i].userId,
                    name: res.data.data[i].name
                })
            }
        } else {
            message.error('获取失败！' + res.data.msg)
        }
    }).catch(err => {
        message.error('请求失败！'+ err);
    })
}

// 获取部门列表
const departmentList = ref(['部门a', '部门b'])
const getDepartmentList = () => {
    getDepartmentListApi().then(res => {
        if (res.data.code === 200) {
            departmentList.value = []
            for (let i = 0; i < res.data.data.length; i++) {
                departmentList.value.push(res.data.data[i])
            }
        } else {
            message.error('获取失败！'+ res.data.msg)
        }
    }).catch(err => {
        message.error('请求失败！'+ err)
    })
}

// 处理翻页
const handleTableChange = (e) => {
    pagination.current = Number(e.current); //更新当前页码
    pagination.pageSize = e.pageSize; //更新每页显示条目数
    // sessionStorage.setItem('currentIndex', pagination.current)
    getUserInfoList()  // 重新获取列表数据
}

// 编辑个人信息弹窗
const openPersonalInfo = ref(false);
const modelInfo = ref(
    {
        id: '',
        name: '',
        workNum: '',
        department: '',
        supervisor1: '',
        supervisor2: '',
        supervisor3: '',
        hr: '',
        supervisorName1: '',
        supervisorName2: '',
        supervisorName3: '',
        hrName: '',
        superAdminName: '',
        firstAdminName: '',
        secondAdminName: '',
        hireDate: '',
        residence: '',
        phoneNumber: '',
        email: '',
        businessType: '',
        lxyz: '',
        role: '',
        password: ''
    }
)
const editRecord = (record) => {
    addNewUserFlag.value = false
    if (isSuperAdmin.value === true) {
        // 回显信息
        modelInfo.value = { ...record }
        // 处理日期显示
        modelInfo.value.hireDate = dayjs(record.hireDate, 'YYYY-MM-DD');
        showModal()
    } else {
        message.error('您暂无编辑权限哦！')
    }
}

// 调用api-删除用户（单个）
const delRecord = (record) => {
    if (isSuperAdmin.value === true) {
        getDeleteUserApi(record.id).then(res => {
            if (res.data.code === 200) {
                message.success('删除成功！')
                getUserInfoList()
            } else {
                message.error('删除失败！' + res.data.msg)
            }
        }).catch(err => {
            message.error('请求失败！'+ err)
        })
    } else {
        message.error('您暂无删除权限哦！')
    }
}

// 调用api-删除用户（批量）
const delManyRecord = () => {
    const delManyIds = Object.values(state.selectedRowKeys)
    if (delManyIds.length === 0) {
        message.warn('请选择要删除的数据！')
        return;
    }
    if (isSuperAdmin.value === true) {

        getDeleteUserManyApi(delManyIds).then(res => {
            if (res.data.code === 200) {
                message.success('删除成功！')
                state.selectedRowKeys = []
                getUserInfoList()
            } else {
                message.error('删除失败！' + res.data.msg)
            }
        }).catch(err => {
            message.error('请求失败！'+ err)
        })
    } else {
        message.error('您暂无删除权限哦！')
    }
}

// 多选框配置
const state = reactive({
    selectedRowKeys: [],
});
const onSelectChange = selectedRowKeys => {
    state.selectedRowKeys = selectedRowKeys;
};

const showModal = () => {
    openPersonalInfo.value = true;
};

const handleCancel = () => {
    openPersonalInfo.value = false;
    modelInfo.value = {
        id: '',
        name: '',
        workNum: '',
        department: '',
        supervisor1: '',
        supervisor2: '',
        supervisor3: '',
        hr: '',
        supervisorName1: '',
        supervisorName2: '',
        supervisorName3: '',
        hrName: '',
        superAdminName: '',
        firstAdminName: '',
        secondAdminName: '',
        hireDate: '',
        residence: '',
        phoneNumber: '',
        email: '',
        businessType: '',
        lxyz: '',
        role: '',
        password: ''
    }
};
// 调用api-修改个人信息（or 新增用户）
const isSubmitting = ref(false);
const handleOk = () => {
    if (isSubmitting.value) {
        message.warning("正在提交中，请勿重复提交！");
        return; // 正在提交中，直接返回，避免重复提交
    }
    isSubmitting.value = true;
    const personalInfo = modelInfo.value
    if (personalInfo.workNum === '' || personalInfo.name === '' || personalInfo.department === '' || personalInfo.hireDate === '' || personalInfo.phoneNumber === '' || personalInfo.email === '' || personalInfo.businessType === '' || personalInfo.lxyz === '' || personalInfo.role === '') {
        message.error('您还有必填信息未填写哦~')
    } else {
        // 处理入职时间格式
        const hireDate = personalInfo.hireDate.$d
        const year = hireDate.getFullYear();
        const month = String(hireDate.getMonth() + 1).padStart(2, '0'); // 月份从0开始，需要加1
        const day = String(hireDate.getDate()).padStart(2, '0'); // 确保天数是两位数
        const hireDateNew = `${year}-${month}-${day}`
        // 处理角色
        const roleMap = {
            '普通用户': '0',
            '一级管理员': '1',
            '二级管理员': '2',
            '超级管理员': '3',
        };
        personalInfo.role = roleMap[personalInfo.role] || personalInfo.role;

        if (addNewUserFlag.value === false) {
            // 调用api-修改信息
            getEditPersonalInfoApi(personalInfo, hireDateNew).then(res => {
                if (res.data.code === 200) {
                    message.success('修改成功！')
                    getUserInfoList()
                    openPersonalInfo.value = false;

                    modelInfo.value = {
                        id: '',
                        name: '',
                        workNum: '',
                        department: '',
                        supervisor1: '',
                        supervisor2: '',
                        supervisor3: '',
                        hr: '',
                        supervisorName1: '',
                        supervisorName2: '',
                        supervisorName3: '',
                        hrName: '',
                        superAdminName: '',
                        firstAdminName: '',
                        secondAdminName: '',
                        hireDate: '',
                        residence: '',
                        phoneNumber: '',
                        email: '',
                        businessType: '',
                        lxyz: '',
                        role: '',
                        password: ''
                    }
                } else {
                    message.error('修改失败！' + res.data.msg)
                }
            }).catch(err => {
                console.log(err)
            }).finally(() => {
            isSubmitting.value = false; // 无论成功还是失败，都将提交状态置为 false
        });
        } else {
            // 清空展示框
            modelInfo.value = {
                        id: '',
                        name: '',
                        workNum: '',
                        department: '',
                        supervisor1: '',
                        supervisor2: '',
                        supervisor3: '',
                        hr: '',
                        supervisorName1: '',
                        supervisorName2: '',
                        supervisorName3: '',
                        hrName: '',
                        superAdminName: '',
                        firstAdminName: '',
                        secondAdminName: '',
                        hireDate: '',
                        residence: '',
                        phoneNumber: '',
                        email: '',
                        businessType: '',
                        lxyz: '',
                        role: '',
                        password: ''
            }
            // 调用api-新增用户
            getAddUserApi(personalInfo, hireDateNew).then(res => {
                if (res.data.code === 200) {
                    message.success('添加成功！')
                    getUserInfoList()
                    openPersonalInfo.value = false;
                } else {
                    message.error('添加失败！' + res.data.msg)
                }
            }).catch(err => {
                console.log(err)
            }).finally(() => {
            isSubmitting.value = false; // 无论成功还是失败，都将提交状态置为 false
        });
        }
    }
};

// 引用文件输入框
const fileInput = ref(null);
// 加载状态，控制加载动画
const loading = ref(false);

// 触发文件选择框
const triggerFileInput = () => {
    fileInput.value.click();
};

// 用户信息导入失败提示框
const openNotificationWithIcon = (type, msg) => {
    notification[type]({
        message: '用户信息导入情况',
        description: msg,
        duration: null
    });
};

// 导入用户信息
const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
        // 开始上传时显示加载动画
        loading.value = true;

        // 调用上传接口
        getUserInfoExcelApi(file).then(res => {
            // 请求成功后的操作
            loading.value = false; // 隐藏加载动画
            if (res.data.code === 200) {
                message.success('文件上传成功~ =• ֊ •=');
                // 刷新页面
                setTimeout(() => {
                    location.reload();
                }, 500); // 延迟0.5秒刷新页面
            } else if(res.data.code === 401){
                const msg1 = '1、未导入的用户：' + res.data.data.notImport + '；      ';
                const msg2 = '2、部分信息导入失败的用户：' + res.data.data.notCompleted;
                openNotificationWithIcon('warn', msg1 + msg2 || '文件上传失败，未知原因');
                // 刷新表格
                getUserInfoList()
                // 上传失败后，重置文件输入框，便于用户重新选择文件
                event.target.value = ''; // 重置文件选择框
            }else {
                openNotificationWithIcon('error', res.data.msg || '文件上传失败，未知原因');
                // 刷新表格
                getUserInfoList()
                // 上传失败后，重置文件输入框，便于用户重新选择文件
                event.target.value = ''; // 重置文件选择框
            }
        })
            .catch(err => {
                loading.value = false; // 隐藏加载动画
                message.error('文件上传失败' + err);
                // 上传失败后，重置文件输入框，便于用户重新选择文件
                event.target.value = ''; // 重置文件选择框
            });
    }
};

// 获取当前时间并格式化
const getCurrentDateTime = () => {
    const now = new Date();
    const year = now.getFullYear();
    const month = (now.getMonth() + 1).toString().padStart(2, '0'); // 月份是从0开始的
    const day = now.getDate().toString().padStart(2, '0');
    return `${year}-${month}-${day}`;
};

// 导出用户信息模板
const exportTemplate = () => {
    getUsersExcel().then((res) => {
        // 判断响应是否为文件流
      if (res && res.data) {
        const blob = new Blob([res.data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
        const link = document.createElement('a');
        const url = URL.createObjectURL(blob);

        // 使用当前日期来动态生成文件名
        const fileName = `用户信息模板${getCurrentDateTime()}.xlsx`;

        link.href = url;
        link.download = fileName;  // 设置文件名
        link.click();  // 触发下载
        message.success("用户信息模板导出成功！");
      } else {
        message.error("导出失败！未知错误");
      }
    }).catch(err => {
        console.log(err)
    });
}

// 新增用户配置
const addNewUserFlag = ref(false)
const addNewUser = () => {
    addNewUserFlag.value = true
    openPersonalInfo.value = true;
};
// 初始加载数据
onMounted(() => {
    getUserInfoList(),
        getDepartmentList(),
        getUserList(),

        updateScrollHeight();
    window.addEventListener('resize', updateScrollHeight);
})

// 在组件卸载时移除事件监听
onUnmounted(() => {
    window.removeEventListener('resize', updateScrollHeight);
});
</script>

<template>
    <div class="user-manage">
        <!-- 表格顶部区域 -->
        <div class="content-header">
            <!-- 搜索表单 -->
            <div class="search-form">
                <a-form style="display: flex;align-items: center;">
                    <a-form-item style="margin-right: 10px; width:150px">
                        <a-input placeholder="输入用户名" v-model:value="serchInfo.name" :allowClear="true" />
                    </a-form-item>
                    <a-form-item style="margin-right: 10px; width:150px">
                        <a-select placeholder="请选择部门" v-model:value="serchInfo.department" :allowClear="true">
                            <a-select-option v-for="item in departmentList" :key="item" :value="item">{{ item
                                }}</a-select-option>
                        </a-select>
                    </a-form-item>
                    <a-form-item style="margin-right: 10px; width:150px">
                        <a-select placeholder="请选择角色" v-model:value="serchInfo.role" :allowClear="true">
                            <a-select-option value="0">普通用户</a-select-option>
                            <a-select-option value="1">一级管理员</a-select-option>
                            <a-select-option value="2">二级管理员</a-select-option>
                            <a-select-option value="3">超级管理员</a-select-option>
                        </a-select>
                    </a-form-item>
                    <a-form-item>
                        <a-button type="primary" @click.prevent="searchSubmit" class="seachBtn">搜索</a-button>
                        <a-divider type="vertical" style="height: 20px; background-color: rgba(0,0,0,0.3)" />
                    </a-form-item>
                    <a-form-item>
                        <a-button @click="addNewUser()">新增用户</a-button>
                    </a-form-item>
                </a-form>
            </div>
            <!-- 右侧按钮 -->
            <div class="submit-Btn">
                <a-popconfirm title="确定要删除吗？" ok-text="确定" cancel-text="取消" @confirm="delManyRecord">
                    <a-button type="primary" danger ghost style="margin-right: 10px;">
                        批量删除
                    </a-button>
                </a-popconfirm>
                <a-tooltip placement="topRight" color="rgba(0,0,0,0.6)">
                    <template #title>提示：导入数据将基于系统现有数据进行覆盖！(⁎˃ᴗ˂⁎)</template>
                    <a-button type="primary" style="margin-right: 10px; background-color: #39ba06;"
                    @click="triggerFileInput">
                    Excel导入
                    </a-button>
                </a-tooltip>
                <a-button type="primary" @click="exportTemplate">导出模板</a-button>
                <!-- 隐藏的文件上传输入框 -->
